#myId {
  border: 0;
  margin: 0 auto;
  display: block;
  border-radius: 10px;
  max-width: 1200px;
  min-width: 900px;
  box-shadow: 3px 0px 11px 4px rgb(255, 255, 255);
  height: 660px;
  width: 100%;
}

.bottomnavbar {
  /* box-shadow: 3px 0px 11px 4px rgb(255, 255, 255); */
  /* background-color: #280657; */
  border-top: 1px solid rgb(215, 134, 247);
  padding-top: 20px;
  background-image: linear-gradient(to right, #000000 , #18001f,#000000);
}

.liveBoxImg{
  width: 80px;
  /* margin-left: -30px;
  margin-right: -20px; */
}

.livePriceBox {
  border: 1px solid #290c33;
  /* box-shadow: 0.2px 0px 4px 2px rgb(255, 255, 255); */
  margin: 0 auto;
  padding: 10px 15px;
  padding-right: 25px;
  color: rgb(37, 35, 37);
  /* animation */
  transition: 0.5s;
  transform-origin: right top;
  border-radius: 10px;
}

.livePriceBox:hover{
  transform: translate(0px, -30px);
  color: white;
  border: 2px solid rgb(189, 158, 197);
}

.LiveRates {
  position: absolute;
  left: -20px;
  background-color: yellow;
  color: #000;
  padding: 7px;
  padding-left: 40px;
  padding-right: 20px;
  cursor: pointer;
  position: fixed;
  border-radius: 30px;
  font-family: "Times New Roman", Times, serif;
  font-size: 20px;
  z-index: 99;
}

.fa-times {
  position: absolute;
  right: 20px;
  margin-top: -110px;
  font-size: 20px;
  color: white;
  border: 2px solid #eee;
  border-radius: 50%;
  padding: 5px 7px;
  cursor: pointer;
}

.fa-arrow-right {
  margin-left: 10px;
}

.navlinks {
  margin-left: 30px;
  font-size: 20px;
}

.navlinks:hover {
  color: #9c0e8f;
  border-bottom: 2px solid #9c0e8f;
}